.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: var(--great-transition);
  background: var(--bg-color);
  opacity: 1;
  z-index: 10000;
}
.preloader.preloader--hide {
  opacity: 0;
  visibility: hidden;
}
.preloader .loader {
  background: var(--expert-color);
  -webkit-animation: load1 1.3s infinite ease-in-out;
  animation: load1 1.3s infinite ease-in-out;
  width: 1em;
  height: 4em;
  color: var(--lawyer-color);
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 1.2em;
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.preloader .loader:before, .preloader .loader:after {
  background: var(--expert-color);
  -webkit-animation: load1 1.3s infinite ease-in-out;
  animation: load1 1.3s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.preloader .loader:before, .preloader .loader:after {
  position: absolute;
  top: 0;
  content: "";
}
.preloader .loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.42s;
  animation-delay: -0.42s;
}
.preloader .loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%, 80%, 100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%, 80%, 100% {
    box-shadow: 0 0;
    height: 1em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 1em;
  }
}

@media screen and (max-width: 999px) {
  .preloader .loader {
    font-size: 1em;
  }
}
@media screen and (max-width: 600px) {
  .preloader .loader {
    font-size: 0.8em;
  }
}
