ol li {
  position: relative;
  margin: 0 0 0 0;
  font-size: inherit;
  box-sizing: border-box;
}
ol li:not(:last-child) {
  margin-bottom: 2rem;
}
ol li:before {
  content: counter(num) ".";
  counter-increment: num;
  display: inline-block;
  position: absolute;
  font-weight: 600;
  top: 0px;
  left: -28px;
  width: auto;
  color: var(--expert-color);
