.bat {
  position: fixed;
  top: 0;
  left: 0;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  transition: all 0ms ease;
  transform: translate(0, 0);
  z-index: 10000;
}
.bat svg {
  width: 100%;
  height: auto;
}
.bat svg path {
  transition: all 100ms ease;
}
.bat--lawyer svg path {
  stroke: var(--lawyer-color);
  fill: var(--lawyer-color);
}
.bat--expert svg path {
  stroke: var(--expert-color);
  fill: var(--expert-color);
}
.bat--light svg path {
  stroke: var(--light);
  fill: var(--light);
}

* {
  cursor: none !important;
}

.btn {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  padding: 1.5rem 2rem;
  font-size: 1.4rem;
  text-decoration: none;
  min-height: 5.6rem;
  border: 2px solid var(--light-dark);
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.1;
  border-radius: 0.5rem;
  transition: all 100ms ease;
  transform: scale(1);
  box-sizing: border-box;
}
.btn:active {
  transform: scale(0.96);
}
.btn--lawyer {
  background-color: var(--lawyer-color);
  border: 2px solid var(--lawyer-color);
  color: var(--light);
}
.btn--lawyer svg path {
  stroke: var(--light);
}
.btn--expert {
  background-color: var(--expert-color);
  border: 2px solid var(--expert-color);
  color: var(--light);
}
.btn--expert svg path {
  stroke: var(--light);
}
.btn--light {
  background-color: var(--light);
  border: 2px solid var(--light);
  color: var(--expert-color);
}
.btn--upp {
  padding-top: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}
.btn--with-elem-left svg {
  margin-right: 1rem;
  transition: var(--great-transition);
}
.btn--with-elem-right svg {
  margin-left: 1rem;
  transition: var(--great-transition);
}
.btn--expert-empty {
  background-color: transparent;
  color: var(--dark);
}
.btn--light-empty {
  background-color: transparent;
  color: var(--light);
}
.btn-burger .btn-burger__cross {
  position: relative;
  display: block;
  width: 11px;
  height: 11px;
  margin-right: 1rem;
}
.btn-burger .btn-burger__cross span {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 3px;
  background-color: var(--light);
  transform: translateY(-50%);
  transition: var(--great-transition);
}
.btn-burger .btn-burger__cross::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--light);
  transition: var(--great-transition);
}
.btn-burger .btn-burger__cross::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--light);
  transition: var(--great-transition);
}
.btn:hover {
  text-decoration: none;
