.btn-file svg {
  margin-right: 1rem;
  width: 2.4rem;
  height: 2.4rem;
}
.btn-file svg path {
  transition: var(--great-transition);
  fill: var(--expert-color);
  stroke: var(--expert-color);
}
.btn-file:hover {
  color: var(--lawyer-color);
  border: 2px dashed var(--lawyer-color);
}
.btn-file:hover span {
  background-color: var(--lawyer-color);
}
.btn-file--open span {
  flex-shrink: 0;
  display: block;
  margin-right: 1rem;
  width: 2.4rem;
  height: 2.4rem;
  background-color: var(--expert-color);
  mask: url(../img/plus.svg) no-repeat center center;
  mask-size: contain;
  transition: var(--great-transition);
