.btn-file--upload span {
  flex-shrink: 0;
  display: block;
  margin-right: 1rem;
  width: 2.4rem;
  height: 2.4rem;
  background-color: var(--light);
  mask: url(../img/file-upload.svg) no-repeat center center;
  mask-size: contain;
  transition: var(--great-transition);
}
.btn-file--upload:hover {
