.great-shadow{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start; /* .  */
    align-items: flex-start;
    z-index: 99; 
    background-color: rgba(22, 22, 36, 0.7);
    z-index: 1000;
    transition: var(--great-transition);
    box-sizing: border-box;
    opacity: 0;
    visibility: hidden;
    &--show{
        opacity: 1;
        visibility: visible;
 
    }
}

.modal-wr{
    flex-shrink: 0;
    flex-grow: 0;
    width: 100%;
    min-height: 100%;
    margin: auto;
    display: none;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;  
    margin: auto; 
    padding: 1.5rem;
    box-sizing: border-box;
    z-index: 1010;  

    &.active{ 
        display: flex;
        .modal{
            opacity: 1;
            visibility: visible;
        }
    }
}
.modal{
    position: relative;
    flex-shrink: 0;
    flex-grow: 0;
    max-width: 480px;
    width: 100%;
    padding: 5rem 4.75%;
    background-color: var(--light);
    box-sizing: border-box; 
    font-size: 1.6rem;
    opacity: 0;
    visibility: hidden;
    transition: var(--great-transition); 
    .modal__one-elem{
        font-size: 1.4rem;
        a{
            font-size: 1.4rem;
        }
    }
    .modal__two-elem{ 
        font-size: 1.4rem;
        a{
            font-size: 1.4rem;
        }
    }
    .modal__caption{
        margin-bottom: 2rem;
    }
    .form-error-box{
        position: relative;
        margin-bottom: 1.6rem;
        overflow: hidden;
        li:not(:first-child){
            margin-top: 1rem;
        }
    }
    button[type="submit"]{
        margin-top: 4rem;
    }
    a{
        text-decoration: none;
        &:hover{
            text-decoration: underline;
        }
    }
    .form-elem-checkbox{
        i{
            border: 1px solid var(--bg-color); 
        }
        .form-elem-checkbox__caption{
            font-size: 1.4rem;
        }
    }
    
}
.modal-message__message-box{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 15rem;
    font-size: 2.3rem;
    line-height: 1.4;
    font-weight: 600;
    color: var(--dark);
    text-align: center;
}
.response-from-server{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: var(--light);
    font-size: 2.3rem;
    line-height: 1.4;
    font-weight: 600;
    color: var(--dark);
    transition: var(--great-transition);
    opacity: 0;
    visibility: hidden;
    &.active{
        display: flex;
        opacity: 1;
        visibility: visible;
    }
}
.modal-auth-reg{
    .form-elem{ 
        margin-bottom: 2rem;
    }
    .modal__two-elem{
        gap: 1rem;
    }
    .modal-auth-reg__footer{
        margin-top: 4rem;
        text-align: center;
        font-size: 1.6rem;
    }

}

.course-review-modal{
    .modal{
        max-width: 70rem;
    }
    .form-elem__textarea{
        min-height: 20rem;
    }
    .form-elem{
        margin-bottom: 2rem;
    }
    .star-rating{
        margin-bottom: 2rem;
        .star-rating__item{
            width: 4rem;
            height: 4rem;
            &:not(:last-child) {
                margin-right: 1rem;
            }
        }
    }
}